import { useCallback, useReducer } from "react";

import {
  E_SiteSearchActions,
  T_SiteSearchAction,
  T_SiteSearchState
} from "./types";
import { debounce } from "helpers/optimization";
import { fuzzySearch } from "models/siteSearch";
import { toNumber } from "helpers/number";

export default function useSiteSearch() {
  const [state, dispatch] = useReducer(siteSearchReducer, {
    inputValue: "",
    searchResults: null
  });

  const updateSearchResults = useCallback(
    debounce(
      () => dispatch([E_SiteSearchActions.setSearchResults]),
      200 // Update search results if 100ms passed after user has stopped typing
    ),
    []
  );

  const handleInputValueChange = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    dispatch([E_SiteSearchActions.setInputValue, event.target.value]);
    updateSearchResults();
  };

  return {
    ...state,
    handleInputValueChange
  };
}

function siteSearchReducer(
  state: T_SiteSearchState,
  [type, payload]: T_SiteSearchAction
): T_SiteSearchState {
  switch (type) {
    case E_SiteSearchActions.setInputValue: {
      return {
        ...state,
        inputValue: payload
      };
    }
    case E_SiteSearchActions.setSearchResults: {
      const inputValueStr = String(state.inputValue);
      const newSearchResults = fuzzySearch.search(inputValueStr);

      // Do not update state if new search results are empty
      if (
        inputValueStr.length > 2 &&
        toNumber(state.searchResults?.length) > 1 &&
        newSearchResults.length === 0
      ) {
        return state;
      }

      return {
        ...state,
        searchResults: newSearchResults
      };
    }
    default:
      return state;
  }
}
